.display{
    position: relative;
    display: inline;
    height: 100vh;
    width: 100vw;
}

.sidePanel{
    position: fixed;
    display: flex;
    bottom: 0;
    transform: rotate(-90deg);
    width: 40px;
    padding: 15px;
}

.buttonC, .button{
    text-transform: uppercase;
    letter-spacing: 1px;
    white-space: nowrap;
    color: var(--font-color);
    font-weight: 100;
    cursor: pointer;
    transition: 0.3s ease-in-out;
}

.buttonC{
    margin-right: 30px; 
    font-size: 14px;

}

.button{
    font-size: 16px;
    letter-spacing: 1px;
    border-bottom: 0.5px solid var(--font-color);
    &:hover{
        cursor: pointer;
        background: var(--background);
    }
}

.boxW, .boxB, .boxR {
    display: inline-block;
    width: 10px; 
    height: 10px; 
    margin-right: 10px; 
    cursor: pointer;
    border: 1px solid var(--font-color);
}

.boxW{background-color: white; }
.boxB {background-color: black; }
.boxR {background-color: blueviolet; }

.main {
    position: relative;
    height: 100vh;
    margin: 0px 50px;
    width: calc(100vw - 100px);
    padding: 60px 0px 0px 10px;
}

.options{
    border-top: 0.5px solid var(--font-color);
    display: flex;
    justify-content: space-between;
    margin-top: 50px;
}
.left{
    display: grid;
    width: 150px;
    height: 300px;
}

.right{
    display: grid;
    width: calc(100vw - 260px);
    padding-left: 40px;
    min-width: 540px;
    justify-content: center;
}

.account, .orders, .preferences, .addresses{
    display: none;
    &.show{
        display: grid
    }
}

.container {
    overflow-y: auto; 
    max-height: calc(100vh - 300px); 
    padding: 20px 40px 15vh 0px;
    border-top: 1px solid var(--font-color); 
    border-bottom: 1px solid var(--font-color); 
}
.container::-webkit-scrollbar {
    display: none; 
}

.projects{
    font-weight: 100;
}

.menuShow{
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 5px 60px 0px 100px;
    margin: 0;
    //background: $faded;
    overflow: hidden;
    z-index: 2;
  }

  .backBlur {
    position: fixed;
    top: 0;
    width: 100vw; /* I'm assuming you meant width: 100%; */
    height: 100vh;
    padding: 0px;
    background: black;
    opacity: 0;
    z-index: -999;
    transition: cubic-bezier(0.075, 0.82, 0.165, 1);
}

.backBlur.show {
    opacity: 0.8;
    z-index: 1;
}

.progressbar {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    height: 5px;
    background: var(--font-color);
    transform-origin: 0%;
    transition: cubic-bezier(0.075, 0.82, 0.165, 1);
  }
  

@media (max-width: 1320px) {
    .projects{
        font-size: 32px;
    }
}

@media (max-width: 900px) {
    .projects{
        font-size: 24px;
    }
}

@media (max-width: 675px) {
    .options{
        border-top: 0px;
        display: grid;    
        width: 100%;    
    }

    .left{
        display: grid;
        height: 60vh;
        width: 100%;
        justify-content: center;
    }
    
    .right{
        display: grid;
        width: 100vw;
        padding-left: 0px;
        min-width: 0px;
        justify-content: left;
    }

    .name{
        font-size: 28px;
    }

    .navbar {
        position: fixed;
        cursor: pointer;
        top: 0;
        right: 0;
        border-radius: 100%;
        border: 1px solid var(--font-color);
        height: 80px;
        width: 80px;
        display: flex;
        align-items: center;
        justify-content: center;
        z-index: 2;
        margin: 40px 30px;
        transition: 0.3s ease-in-out;
        &:hover {
          color: white;
          background: black;
        }
      }

    .menuButtonContainer{
        width: 100%;
        justify-content: center;
        display: flex;
        justify-content: flex-end;
    }

    .menuShow{
        display: block;
        position: fixed;
        right: 0;
        top: 0;
        height: 100vh;
        width: 250px;
        padding: 20px 20px 20px 20px; 
        background-color: var(--background);
        box-shadow: -2px 0 4px rgba(0, 0, 0, 0.1);
        transition: transform 0.3s ease-in-out;
        transform: translateX(100%);
        z-index: 5;
    }
    
    .menuShow.show{
        transform: translateX(0%);
        justify-content: center;
    }

 

    .sidePanel{
        position: fixed;
        display: grid;
        bottom: 0;
        left: 0;
        transform: rotate(0);
        padding: 15px 0px;
    }

    .sidePanelHeader{
        position: relative;
        display: grid;
        justify-content: center;
        border-bottom: 2px solid var(--font-color);
    }
}

@media (max-width: 575px) {
    .navbar {
        color: var(--font-color);
        background: var(--background);
        &:hover {
            color: var(--background);
            background: var(--font-color);
        }
      }
}